.wrapper {
  margin: 27px 60px;
  display: flex;
  justify-content: flex-end;
}

.menu_list {
  list-style: none;
  display: flex;
  gap: 30px;
}

.menu_item {
  font-size: 20px;
  font-weight: 500;
  text-decoration: none;
  color: #000;
}

.menu_list :global(a.active) {
  color: #FF6464;
}